<template>
  <div class="collection">
    <div class="rg-top-top">
      <span class="content-top-text-blue"></span>
      <strong style="margin-left: 2px">{{company[0].shenbao_month}} 其他附表</strong>
    </div>
    <div class="rg-content">
      <div style="margin-left: 20px">1、综合所得申报表中填写了“减免税额”的需填写减免事项附表</div>
      <div style="margin-left: 20px">2、在本期其他扣除中填写了“商业健康保险” 的需填写商业健康保险附表</div>
    </div>
    <div style="border: solid 1px #e0e0e0;margin: 0 10px; width:calc(100% - 22px)">
      <div id="con_in" class="con-div" style="overflow-y: auto; display: block;">
        <el-table :data="extraTableData" border>
          <el-table-column prop="name" label="附表名称"></el-table-column>
          <el-table-column prop="personNumber" label="填写人数"></el-table-column>
          <el-table-column prop="money" label="金额合计"></el-table-column>
          <el-table-column prop="status" label="填写状态"></el-table-column>
          <el-table-column prop="editor" label="操作"></el-table-column>
        </el-table>
      </div>
    </div>

  </div>
</template>
<script>
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      extraTableData: []
    }
  },
  created () {
    this.$emit('router', this.$router.history.current.name)
    this.$emit('pathNumber', 3)
    this.setExtraTable()
  },
  computed: {
    ...mapGetters(['company'])
  },
  methods: {
    showNull () {
      this.$alert('本系统为教学系统，目前只提供常用操作功能噢', '信息', {
        confirmButtonText: '确定'
      })
    },
    nullClick () {
      this.showNull()
    },
    setExtraTable () {
      let extraTableData = []
      let name = ['减免事项附表', '商业健康保险附表', '税延养老保险附表']
      for (let i in name) {
        extraTableData[i] = {}
        extraTableData[i].name = name[i]
        extraTableData[i].personNumber = 0
        extraTableData[i].money = '0.00'
        extraTableData[i].status = '无需填写'
        extraTableData[i].editor = '填写'
      }
      this.extraTableData = extraTableData
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  div {
    font-size: 14px;
  }

  h1,
  h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  .el-table {
    font-size: 14px;
  }

  .rg-content {
    line-height: 18px;
    margin: 0 0 10px 0;
  }

  .rg-top-top {
    height: 40px;
    overflow: hidden;
    line-height: 40px;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    margin: 10px 10px 0 10px;
  }

  .top-div {
    flex: 1;
    background-color: #f4f6f9;
    color: #666666;
    text-align: center;
    border-radius: 3px;
    cursor: pointer;
  }
</style>



